<template>
  <main class="app-main">
    <section class="top">
      <p class="title">
        <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>编辑基本信息
      </p>
    </section>
    <div class="con-con">
      <div class="image-reader-item add">
        <md-image-reader
          name="reader0"
          @select="onReaderSelect"
          @complete="onReaderComplete"
          @error="onReaderError"
          is-multiple
        ></md-image-reader>
        <div class="des">头像</div>
        <img class="head-pic" :src="userInfo.img" alt />
      </div>
      <md-input-item
        ref="input0"
        title="平台账号"
        readony
        readonly="readonly"
        is-amount
        :clearable="true"
        v-model="userInfo.userName"
        class="read"
      ></md-input-item>
      <md-input-item
        ref="input0"
        title="认证身份"
        is-amount
        readonly="readonly"
        :clearable="true"
        v-model="type"
        class="read"
      ></md-input-item>
      <md-input-item
        ref="input0"
        title="关联电话"
        readonly="readonly"
        is-amount
        :clearable="true"
        v-model="userInfo.phone"
      >
        <md-icon slot="right" name="arrow-right" size="lg" @click="iconDialog.open = true"></md-icon>
      </md-input-item>
      <md-input-item
        ref="input0"
        title="关联邮箱"
        is-amount
        :clearable="true"
        v-model="userInfo.email"
        readonly="readonly"
      >
        <md-icon slot="right" name="arrow-right" size="lg" @click="iconDialog1.open = true"></md-icon>
      </md-input-item>
      <md-input-item
              ref="input0"
              title="所属地区"
              is-amount
              :clearable="true"
              v-model="city"
              readonly="readonly"
      >
        <md-icon slot="right" name="arrow-right" size="lg" @click="show = true"></md-icon>
      </md-input-item>
      <md-input-item ref="input0" title="工作单位" is-amount :clearable="true" v-model="userInfo.unit"></md-input-item>
      <md-input-item
        ref="input0"
        title="工作职务"
        placeholder="工作职务"
        is-amount
        :clearable="true"
        v-model="userInfo.position"
      ></md-input-item>
      <!-- <md-input-item
                    ref="input0"
                    title="关联邮箱"
                    placeholder="邮箱"
                    is-amount
                    :clearable="true"
                    v-model="userInfo.email"
      ></md-input-item>-->
      <!-- <md-input-item
                    ref="input0"
                    title="工作单位"
                    :clearable="true"
                    placeholder="工作单位"

                    v-model="renderInfo.userUnit"
                    is-amount
            >
            </md-input-item>
            <md-input-item
                    ref="input0"
                    title="工作职务"
                    :clearable="true"
                    placeholder="工作职务"

                    v-model="renderInfo.post"
                    is-amount
            >
      </md-input-item>-->
      <md-tab-picker
              title="所在地区"
              describe=""
              large-radius
              :data="areaData"
              v-model="show"
              v-if="show"
              @select="handleSelect"
              @change="handleChange"/>
      <md-input-item
        ref="input0"
        title="通讯地址"
        :clearable="true"
        placeholder="通讯地址"
        v-model="userInfo.address"
        is-amount
      ></md-input-item>
    </div>
    <md-button type="default" @click="editInfo">保存</md-button>
    <md-picker
      ref="typePicker"
      v-model="isEducShow"
      :data="Education"
      @confirm="onEducConfirm"
      title="选择操作类型"
    ></md-picker>
    <md-picker
      ref="typePicker"
      v-model="isTypeShow"
      :data="expertType"
      @confirm="onTypeConfirm"
      title="选择操作类型"
    ></md-picker>
    <md-picker
      ref="typePicker"
      v-model="isFieldShow"
      :data="field"
      @confirm="onFieldConfirm"
      title="选择操作类型"
    ></md-picker>
    <md-dialog title="修改关联电话" :closable="true" v-model="iconDialog.open" :btns="iconDialog.btns">
      <div class="telephone">
        <hr />
        <div class="center">
          <div class="list">
            <md-input-item
              title="联系电话"
              ref="input11"
              v-model="phone"
              :clearable="true"
              placeholder="请输入联系电话"
            ></md-input-item>
          </div>
          <div class="list">
            <md-input-item
              title="验证码"
              ref="input11"
              v-model="code"
              :clearable="true"
              placeholder="请输入验证码"
            >
              <div v-if="!isSendShow" class="input-operator" slot="right" @click="sendCode">获取验证码</div>
              <div v-if="isSendShow" class="min" slot="right">{{ num }}秒</div>
            </md-input-item>
          </div>
        </div>
        <div class="foot">
          <button type="button" @click="iconDialog.open=false">取消</button>
          <span></span>
          <button type="button" @click="commitPhone">确定</button>
        </div>
      </div>
    </md-dialog>
    <md-dialog title="修改关联邮箱" :closable="true" v-model="iconDialog1.open" :btns="iconDialog1.btns">
      <div class="telephone">
        <hr />
        <div class="center">
          <div class="list">
            <md-input-item
              title="联系邮箱"
              ref="input11"
              v-model="email"
              :clearable="true"
              placeholder="请输入联系邮箱"
            ></md-input-item>
          </div>
          <div class="list">
            <md-input-item
              title="验证码"
              ref="input11"
              v-model="codeEmail"
              :clearable="true"
              placeholder="请输入验证码"
            >
              <div v-if="!isSendShow" class="input-operator" slot="right" @click="emailGet">获取验证码</div>
              <div v-if="isSendShow" class="min" slot="right">{{ num }}秒</div>
            </md-input-item>
          </div>
        </div>
        <div class="foot">
          <button type="button" @click="iconDialog1.open=false">取消</button>
          <span></span>
          <button type="button" @click="commitEmail">确定</button>
        </div>
      </div>
    </md-dialog>
  </main>
</template>

<script>
import {
  Field,
  CellItem,
  Toast,
  ImageReader,
  FieldItem,
  Picker,
  Icon,
  InputItem,
  TabPicker,
  Button,
  Dialog
} from "mand-mobile";
import { Education, expertType, field } from "../../../utils/select";
import { editInfo, fileUpload, getInfo } from "../../../apiUrl/personal/info";
import { getCity, getArea } from '@/apiUrl/base'
import areaData from '../../../../static/data'
import {
  sendCode,
  getDictionary,
  testCode,
  verifyEmail,
  sendEmail
} from "../../../apiUrl/login";
export default {
  name: "index",
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [ImageReader.name]: ImageReader,
    [Picker.name]: Picker,
    [FieldItem.name]: FieldItem,
    [InputItem.name]: InputItem,
    [TabPicker.name]: TabPicker,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      imageList: {
        // 上传照片列表
        reader0: [
          "//img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/27fb7f097ca218d743f816836bc7ea4a",
          "//manhattan.didistatic.com/static/manhattan/insurancePlatform_spu/uploads/c2912793a222eb24b606a582fd849ab7"
        ],
        reader1: []
      },
      renderInfo: {
        genre: "",
        type: "",
        academic: ""
      },
      iconDialog: {
        open: false
      },
      iconDialog1: {
        open: false
      },
      code: "",
      email: "",
      phone: "",
      codeEmail: "",
      num: 60,
      isSendShow: false,
      show: false,
      city: '',
      userInfo: {},
      type: this.$store.state.user.userInfo.roleId == 1 ? "专家" : "单位",
      isEducShow: false,
      isTypeShow: false,
      isFieldShow: false,
      expertType: [],
      Education: [],
      field: [],
      areaData: areaData,
    };
  },
  mounted() {
    this.getDictionary();
    this.getInfo();
    this.userInfo.intro = window.localStorage.infoText;
    this.$store.getters.userInfo
      ? (this.userInfo = this.$store.getters.userInfo)
      : this.userInfo;
    this.$store.getters.renderInfo
      ? (this.renderInfo = this.$store.getters.renderInfo)
      : this.renderInfo;
    console.log(this.userInfo.intro);
  },
  methods: {
    editInfo() {
      // const args = new FormData
      //  args.append('img', '123')
      console.log(this.userInfo);
      editInfo(this.userInfo).then(res => {
        if (res.status === 200) {
          // this.$router.push('/personal/checkInfo')
        }
      });
    },
    getInfo() {
      getInfo().then(res => {
        this.userInfo = res.data;
      });
    },
    // 回退
    goBack() {
      window.localStorage.infoText = "";
      this.$router.push("/personal");
    },
    onProfile() {
      this.$store.commit("SET_USERINFO", this.userInfo);
      this.$store.commit("SET_RENDINFO", this.renderInfo);
      this.$router.push("/personal/Profile");
    },
    onEducShow() {
      document.activeElement.blur();
      this.isEducShow = true;
    },
    onTypeShow() {
      document.activeElement.blur();
      this.isTypeShow = true;
    },
    handleSelect (data) {
      this.city = ''
      if (data.option.name === 'province') {
        this.areaData = areaData
        this.provinceId = data.option.selected.index
        getCity({provinceid: data.option.selected.id}).then(res => {
          this.areaData.options[this.provinceId].children.options = []
          res.data.forEach((item, key) => {
            this.areaData.options[this.provinceId].children.options.push({
              label: item.city,
              value: item.city,
              id: item.cityid,
              index: key,
              children: {
                'name': 'area',
                'label': '请选择',
                'options': [{
                  'value': '',
                  'label': '',
                  'index': 2
                }]
              }
            })
          })
          // this.areaData.options[this.provinceId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      } else if (data.option.name === 'city') {
        if (data.option.selected) {
          this.cityId = data.option.selected.index
        }
        if (!(data.option.selected && data.option.selected.hasOwnProperty('id'))) {
          return
        }
        getArea({cityid: data.option.selected.id}).then(res => {
          this.areaData.options[this.provinceId].children.options[this.cityId].children.options = []
          res.data.forEach(item => {
            this.areaData.options[this.provinceId].children.options[this.cityId].children.options.push({
              label: item.area,
              value: item.area,
              id: item.areaid
            })
          })
          // this.areaData.options[this.provinceId].children.options[this.cityId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      }
    },
    handleChange (val) {
      val.values.forEach((item, key) => {
        this.city = this.city + item
        switch (key) {
          case 0: this.userInfo.province = item; break
          case 1: this.userInfo.city = item; break
          case 2: this.userInfo.area = item; break
        }
      })
      this.searchParams.pageNumber = 1
      this.getList()
    },
    onFieldShow() {
      document.activeElement.blur();
      this.isFieldShow = true;
    },
    // 类型选择确认
    onEducConfirm(columnsValue) {
      console.log(
        `[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(
          columnsValue
        )}`
      );
      // this.Education = columnsValue[0].text
      this.renderInfo.academic = columnsValue[0].label;
      this.userInfo.academic = columnsValue[0].value;
    },
    // 类型选择确认
    onTypeConfirm(columnsValue) {
      console.log(
        `[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(
          columnsValue
        )}`
      );
      // this.Education = columnsValue[0].text
      this.userInfo.type = columnsValue[0].value;
      this.renderInfo.type = columnsValue[0].label;
    },
    // 类型选择确认
    onFieldConfirm(columnsValue) {
      console.log(
        `[Mand Mobile] DatePicker Confirm\nvalue: ${JSON.stringify(
          columnsValue
        )}`
      );
      // this.Education = columnsValue[0].text
      this.userInfo.genre = columnsValue[0].value;
      this.renderInfo.genre = columnsValue[0].label;
    },
    onReaderSelect(name, { files }) {
      files.forEach(file => {
        console.log(file);
      });
      Toast.loading("图片读取中...");
    },
    onReaderComplete(name, { dataUrl, file }) {
      // this.userInfo.img = dataUrl
      Toast.hide();
      const args = new FormData();
      args.append("file", file);
      args.append("fileName", file.name);
      fileUpload(args)
        .then(res => {
          this.userInfo.img = res.file;
        })
        .catch(e => {
          Toast.failed(e);
        });
    },
    onReaderError(name, { msg }) {
      Toast.failed(msg);
    },
    onDeleteImage(name, index) {
      const demoImageList = this.imageList[name] || [];
      demoImageList.splice(index, 1);
      this.$set(this.imageList, name, demoImageList);
    },
    getDictionary() {
      getDictionary({ type: "genre" }).then(e => {
        this.field.push(e.data);
      });
      getDictionary({ type: "academic" }).then(e => {
        this.Education.push(e.data);
      });
      getDictionary({ type: "professorType" }).then(e => {
        this.expertType.push(e.data);
      });
    },
    emailGet() {
      let reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if (reg.test(this.email)) {
        const args = {
          email: this.email
        };

        sendEmail(args).then(res => {
          if (res.statusMsg === "success") {
            Toast.succeed("邮件已发送！");
          }
        });
        this.isSendShow = true;
        const val = setInterval(() => {
          this.num--;
          if (this.num === 0) {
            clearTimeout(val);
            this.isSendShow = false;
            this.num = 60;
          }
        }, 1000);
      } else {
        Toast.failed("邮箱格式不正确");
      }
    },
    sendCode() {
      if (/^1[3456789]\d{9}$/.test(this.phone)) {
        const args = {
          phone: this.phone
        };
        sendCode(args).then(res => {
          if (res.statusMsg === "success") {
            Toast.succeed("短信已发送！");
          }
        });
        this.isSendShow = true;
        const val = setInterval(() => {
          this.num--;
          if (this.num === 0) {
            clearTimeout(val);
            this.isSendShow = false;
            this.num = 60;
          }
        }, 1000);
      } else {
        Toast.failed("手机号格式不正确");
      }
    },
    commitPhone() {
      if (!this.phone) {
        Toast.failed("请输入联系电话");
        return;
      }
      if (!this.code) {
        Toast.failed("请输入验证码");
        return;
      }
      // 验证码
      let codeData = {
        phone: this.phone,
        code: this.code
      };
      testCode(codeData)
        .then(res => {
          this.iconDialog.open = false;
          this.userInfo.phone = this.phone;
        })
        .catch(e => {
          Toast.failed(e);
        });
    },
    commitEmail() {
      if (!this.email) {
        Toast.failed("请输入联系邮箱");
        return;
      }
      let reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if (!reg.test(this.email)) {
        Toast.failed("邮箱格式有误");
        return;
      }
      if (!this.codeEmail) {
        Toast.failed("请输入验证码");
        return;
      }
      // 验证码
      let codeData = {
        email: this.email,
        code: this.codeEmail
      };
      verifyEmail(codeData)
        .then(res => {
          this.iconDialog1.open = false;
          this.userInfo.email = this.email;
        })
        .catch(e => {
          Toast.failed(e);
        });
    }
  }
};
</script>

<style lang="stylus" scoped>
