  .title
    background: rgba(2, 25, 85, 1)
.con-con
  padding-left: 30px
  margin-bottom: 60px
  margin-top: 88px
  background: rgba(255, 255, 255, 1)
  box-shadow: 0px 4px 24px 0px rgba(2, 25, 85, 0.1)
  border-radius: 12px
  padding-top: 88px
  .head-portrait
    background: rgba(2, 25, 85, 0)
    border-radius: 12px
    img
      width: 140px
      height: 140px
      margin-top: -140px
  .phone
    font-size: 28px
    font-family: PingFang-SC-Medium
    font-weight: 500
    color: rgba(2, 25, 85, 1)
    line-height: 36px
    img
      width: 25px
      height: 28px
      margin-left: 20px
/deep/.md-field-item-title
  color: #666
  font-weight: 20
/deep/.md-cell-item-body
  padding: 0px 20px
  text-align: left
/deep/.md-cell-item-left
  margin-right: 10px
/deep/.md-cell-item
  height: 91px
/deep/.md-cell-item-body:before
  top: 91px
  bottom: auto
/deep/.md-button
  width: 660px
  height: 88px
  background: rgba(2, 25, 85, 1)
  border-radius: 12px
  margin: 0 auto
  color: #fff
/deep/.md-image-reader
  height: 100px
  float: left
/deep/.md-field-item-right
  margin-right: 20px
/deep/.md-image-reader-file
  height: 100px
.image-reader-item
  height: 100px
  color: #666
  font-size: 0.32rem
  line-height: 100%
  font-weight: 20
  text-align: left
  border-bottom: 0.02rem solid #e2e4ea
  position: relative
  .head-pic
    width: 80px
    height: 80px
    position: absolute
    right: 31px
